/* App.css */

body {
  font-family: 'Pretendard JP', sans-serif;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  padding: 10px 20px;
  border-bottom: 1px solid #ddd;
}

.navbar a {
  text-decoration: none;
  color: #333;
  font-size: 16px;
  padding: 5px 10px;
  border-radius: 5px;
  font-family: 'Pretendard JP', sans-serif;
}

.navbar-logo {
  display: flex;
  align-items: center;
}

.navbar-logo img {
  width: 130px; /* 로고의 크기를 크게 설정 */
  margin-right: 10px;
}

.navbar-logo span {
  color: #000000;
  font-size: 1.2rem;
}

.navbar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
}

.navbar li {
  margin-left: 15px;
}

.navbar li a {
  color: #000000;
  text-decoration: none;
}

.navbar li.active a {
  color: #000000;
}

.navbar li .MuiButton-root {
  color: #ffffff;
}

section {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 768px) {
  .navbar ul {
    flex-direction: column;
    width: 100%;
    display: none;
  }

  .navbar ul.show {
    display: flex;
  }

  .navbar li {
    margin: 10px 0;
  }

  .navbar-toggle {
    display: block;
    background-color: #333;
    color: white;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
  }
}

@media (min-width: 769px) {
  .navbar-toggle {
    display: none; /* 화면이 충분히 클 때 햄버거 버튼 숨김 */
  }
}
