.section {
  position: relative;
  width: 100%;
  height: 80vh; /* 필요에 따라 조정 */
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* 반투명한 검은색 오버레이 */
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px;
}

.text-center {
  text-align: center;
}

.text-white {
  color: white;
}

.text-blue {
  color: rgb(34, 34, 197);
}

.text-black {
  color: black;
}

.px-20 {
  padding-left: 20px;
  padding-right: 20px;
}

.font-bold {
  font-weight: bold;
}

.text-h40 {
  font-size: 40px; /* 기본 폰트 크기 */
}

.lg\:text-h26 {
  font-size: 26px; /* 기본 폰트 크기 */
}

.section-spacer {
  height: 30px; /* 섹션 사이의 공간을 위해 높이 조정 */
}

.rounded-image-section {
  margin: 60px auto; /* 중앙 정렬을 위한 자동 마진 */
  width: 75%;
  padding: 30px 20px;
  box-sizing: border-box;
  aspect-ratio: 1.7 / 1;
  border-radius: 20px;
  background: center center / cover;
  display: flex;
  flex-direction: column; /* 텍스트와 박스를 수직으로 배치 */
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
}

.white-boxes {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%; /* 박스들이 중앙에 위치하도록 부모 컨테이너의 너비를 설정 */
  align-items: center;
  margin-top: 20px;
}

.white-box {
  background-color: white;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 약간의 그림자 효과 */
  width: 80%; /* 필요에 따라 조정 */
  max-width: 500px; /* 필요에 따라 최대 너비 설정 */
}

.white-box p {
  margin: 0;
  font-size: 16px; /* 필요에 따라 조정 */
}

.white-box strong {
  font-size: 20px; /* 강조된 텍스트 크기 */
  color: rgb(34, 34, 197);
}

/* 반응형 디자인을 위한 미디어 쿼리 */
@media (max-width: 1200px) {
  .text-h40 {
    font-size: 35px; /* 창 크기가 작아질 때 폰트 크기 조정 */
  }
  .lg\:text-h26 {
    font-size: 22px; /* 창 크기가 작아질 때 폰트 크기 조정 */
  }
  .small-text {
    font-size: 18px; /* 창 크기가 작아질 때 작은 폰트 크기 조정 */
  }
}

@media (max-width: 992px) {
  .text-h40 {
    font-size: 30px; /* 더 작은 화면에서 폰트 크기 조정 */
  }
  .lg\:text-h26 {
    font-size: 20px; /* 더 작은 화면에서 폰트 크기 조정 */
  }
  .small-text {
    font-size: 16px; /* 더 작은 화면에서 작은 폰트 크기 조정 */
  }
}

@media (max-width: 768px) {
  .text-h40 {
    font-size: 25px; /* 태블릿 크기 화면에서 폰트 크기 조정 */
  }
  .lg\:text-h26 {
    font-size: 22px; /* 태블릿 크기 화면에서 폰트 크기 조정 */
  }
  .small-text {
    font-size: 17px; /* 태블릿 크기 화면에서 작은 폰트 크기 조정 */
  }
  .white-box {
    padding: 15px;
  }
  .white-box p {
    font-size: 14px;
  }
  .white-box strong {
    font-size: 18px;
  }
}

@media (max-width: 576px) {
  .text-h40 {
    font-size: 20px; /* 모바일 화면에서 폰트 크기 조정 */
  }
  .lg\:text-h26 {
    font-size: 16px; /* 모바일 화면에서 폰트 크기 조정 */
  }
  .small-text {
    font-size: 12px; /* 모바일 화면에서 작은 폰트 크기 조정 */
  }
  .white-box {
    padding: 10px;
  }
  .white-box p {
    font-size: 12px;
  }
  .white-box strong {
    font-size: 16px;
  }
}
